
const pathModule = require('path');
const fs = require('fs');

import { FileUtils } from './fileUtils';
import { Utilities } from './utilities';
import { SpecialPaths, ConfigPaths, SKILL_MANIFEST_FILE } from './specialPaths';
import { ConfigAccessor } from '@alexa-games/sfb-skill';
import { Logger } from './logger';
import { StdOutput } from './stdOutput';
import { Command } from './command';

/**
 * Run this to deploy just the voice model and skill manifest files, and skill the Lambda file upload.
 * Useful if your lambda is large and you do not want to update it, or if you are uploading your lambda
 * from an S3 linked file.
 * (Optional)
 * Reads: Deploy path
 * Writes: Cloud
 * Depends on: StageCommand
 */
export class DeployMetadataCommand implements Command {

    constructor(
        private readonly storyPath: string,
        private readonly logger: Logger,
        private readonly stdOutput: StdOutput) {
    }

    public async run() {
        this.logger.status("Deploying only Metadata...");

        const dirs = new SpecialPaths(this.storyPath);

        const config = await ConfigAccessor.loadConfigFile(dirs.abcConfig, dirs.builtResourcesPath);

        const configDirs = dirs.getConfigDirectories(config);
       
        const askProfileName = config.getValue("ask-profile-name");

        let startTime = Date.now();

        await this.restoreDeploymentMetadata(dirs, configDirs);

        await Utilities.runCommandInDirectoryAsync(
            Utilities.npxBin,
            [ 'ask', 'deploy', '--target', 'skill', '--no-wait', '--force', '--profile', askProfileName ],
            configDirs.askSkillFullPath,
            this.stdOutput,
            {shell: true});

        await Utilities.runCommandInDirectoryAsync(
            Utilities.npxBin,
            [ 'ask', 'deploy', '--target', 'model', '--no-wait', '--force', '--profile', askProfileName ],
            configDirs.askSkillFullPath,
            this.stdOutput,
            {shell: true});

        await Utilities.runCommandInDirectoryAsync(
            Utilities.npxBin,
            [ 'ask', 'deploy', '--target', 'isp', '--no-wait', '--force', '--profile', askProfileName ],
            configDirs.askSkillFullPath,
            this.stdOutput,
            {shell: true});
        
        await this.preserveDeploymentMetadata(dirs, configDirs);

        let duration = Date.now() - startTime;

        this.logger.success(`Deployment finished in ${duration} ms.`);
    }

    private async restoreDeploymentMetadata(dirs: SpecialPaths, configPaths: ConfigPaths) {
        // skill.json
        const skillManifestFilePath = pathModule.join(dirs.metaDataStoragePath, SKILL_MANIFEST_FILE);

        await this.copyIfPresent(skillManifestFilePath, configPaths.askSkillFullPath);

        // ask_config
        const askConfigFilePath = pathModule.join(dirs.metaDataStoragePath, 'ask_config');
        const skillAskConfigFolder = pathModule.join(configPaths.askSkillFullPath, '.ask');
        const stagedAskConfigFile = pathModule.join(skillAskConfigFolder, 'config');
        FileUtils.makeDir(skillAskConfigFolder);
        if (fs.existsSync(askConfigFilePath)) {
            await FileUtils.copyFile(askConfigFilePath, stagedAskConfigFile);
        }
    }

    private async preserveDeploymentMetadata(dirs: SpecialPaths, configPaths: ConfigPaths) {
        // ask_config
        this.logger.status('Creating a copy of .ask/config for safe keeping...')
        const skillAskConfigFolder = pathModule.join(configPaths.askSkillFullPath, '.ask');
        const stagedAskConfigFile = pathModule.join(skillAskConfigFolder, 'config');
        if (fs.existsSync(stagedAskConfigFile)) {
            FileUtils.makeDir(dirs.metaDataStoragePath);
            const askConfigFilePath = pathModule.join(dirs.metaDataStoragePath, 'ask_config');
            await FileUtils.copyFile(stagedAskConfigFile, askConfigFilePath);
        }
    }

    private async copyIfPresent(src: string, dst: string, message?: string) {
        if (fs.existsSync(src)) {
            if (message) {
                this.logger.status(message)
            }

            await FileUtils.recursiveCopy(src, dst);
        }
    }
}
